import { Story, Chapter, Category } from "./StoryStore";

it("Story Modal", () => {
    const item = Chapter.create({
        title: "Chapter1",
        content: "nothing to be written here"
    })

    expect(item.content).toBe("nothing to be written here")
})


it("Category Modal", () => {
    const item = Category.create({
        type: "Action",
    })
    const item2 = Category.create({
        type: "Fiction",
    })
    const item3 = Category.create({
        type: "Non-Fiction",
    })

    expect(item.type).toBe("Action")
    expect(item2.type).toBe("Fiction")
    expect(item3.type).toBe("Non-Fiction")
})


it("Story", () => {
    const category = Category.create({ type: "Action" })
    const date = new Date()
    const chapter = Story.create({
        name: "Bear2",
        author: "Chan",
        createDate: date,
        chapter: [
            {
                title: "Chapter1",
                content: "nothing to be written here"
            },
            {
                title: "Chapter2",
                content: "nothing to be written"
            },
            {
                title: "Chapter3",
                content: "nothing to be"
            }
        ],
        category: category
    })

    expect(chapter.name).toBe("Bear2")
    expect(chapter.author).toBe("Chan")
    expect(chapter.createDate).toBe(date)
    expect(chapter.chapter).toBe([
        {
            title: "Chapter1",
            content: "nothing to be written here"
        },
        {
            title: "Chapter2",
            content: "nothing to be written"
        },
        {
            title: "Chapter3",
            content: "nothing to be"
        }
    ])
    expect(chapter.category).toBe(category)
})